*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  scrollbar-gutter: stable;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.main-container {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.advantages {
  max-width: 460px;
  margin: 0 auto;
  padding: 31px 30px 29px 30px;
}
@media (min-width: 768px) {
  .advantages {
    width: 768px;
    max-width: 100%;
    padding: 56px 50px 81px 50px;
  }
}
@media (min-width: 1150px) {
  .advantages {
    width: 700px;
    padding: 68px 0 88px 0;
  }
}

.advantages__title {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .advantages__title {
    margin-bottom: 42px;
  }
}
@media (min-width: 1150px) {
  .advantages__title {
    margin-bottom: 39px;
  }
}

.advantages__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  grid-template-columns: 1fr;
  row-gap: 30px;
}
@media (min-width: 768px) {
  .advantages__list {
    column-gap: 1px;
    grid-template-columns: 1fr 1fr;
    row-gap: 55px;
  }
}
@media (min-width: 1150px) {
  .advantages__list {
    column-gap: 0;
  }
}

.advantages__item {
  position: relative;
  padding-top: 53px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
}
.advantages__item::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.advantages__item--flower::before {
  top: -2px;
  left: 8px;
  width: 33px;
  height: 38px;
  background-image: url("../img/svg/flower.svg");
}
@media (min-width: 768px) {
  .advantages__item--flower::before {
    top: -1px;
  }
}
.advantages__item--money::before {
  top: -1px;
  left: 0;
  width: 47px;
  height: 27px;
  background-image: url("../img/svg/money.svg");
}
@media (min-width: 768px) {
  .advantages__item--money::before {
    top: 1px;
    left: 1px;
  }
}
.advantages__item--heart::before {
  top: 4px;
  left: 7px;
  width: 34px;
  height: 27px;
  background-image: url("../img/svg/heart.svg");
}
@media (min-width: 768px) {
  .advantages__item--heart::before {
    top: 6px;
    left: 8px;
  }
}
.advantages__item--skein::before {
  top: 1px;
  left: 3px;
  width: 35px;
  height: 34px;
  background-image: url("../img/svg/skein.svg");
}
@media (min-width: 768px) {
  .advantages__item--skein::before {
    top: 3px;
    left: 2px;
  }
}
.advantages__item--rocket::before {
  top: 2px;
  left: -6px;
  width: 55px;
  height: 23px;
  background-image: url("../img/svg/rocket.svg");
}
@media (min-width: 768px) {
  .advantages__item--rocket::before {
    top: 4px;
    left: -7px;
  }
}
.advantages__item--gift::before {
  top: 0;
  left: 3px;
  width: 33px;
  height: 32px;
  background-image: url("../img/svg/gift.svg");
}
@media (min-width: 768px) {
  .advantages__item--gift::before {
    top: 2px;
    left: 2px;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    padding-top: 56px;
    padding-right: 30px;
    font-size: 20px;
    line-height: 22px;
  }
}
@media (min-width: 768px) {
  .advantages__item--flower {
    order: 1;
  }
}
@media (min-width: 768px) {
  .advantages__item--money {
    order: 3;
  }
}
@media (min-width: 768px) {
  .advantages__item--heart {
    order: 5;
  }
}
@media (min-width: 768px) {
  .advantages__item--skein {
    order: 2;
  }
}
@media (min-width: 768px) {
  .advantages__item--rocket {
    order: 4;
  }
}
@media (min-width: 768px) {
  .advantages__item--gift {
    order: 6;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  padding: 14px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: inherit;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button--primary {
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button--primary:focus {
  outline: none;
}
.button--primary:focus:not(:focus-visible) {
  outline: none;
}
.button--primary:focus-visible {
  border: 2px solid #62d1ba;
  outline: none;
}
.button--primary:hover {
  color: #ffffff;
  background-color: #444444;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  color: #ffffff;
  background-color: #63d1bb;
}
.button--secondary:hover {
  background-color: #56c3b2;
}
.button--secondary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  opacity: 0.3;
  pointer-events: none;
}

.catalog {
  max-width: 460px;
  margin: 0 auto;
  padding: 30px 30px 15px 30px;
}
@media (min-width: 768px) {
  .catalog {
    max-width: 100%;
    padding: 76px 50px 53px 50px;
  }
}
@media (min-width: 1150px) {
  .catalog {
    width: 1150px;
    padding-top: 129px;
    padding-bottom: 57px;
  }
}

.catalog__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  grid-template-columns: 1fr;
  row-gap: 30px;
}
@media (min-width: 768px) {
  .catalog__list {
    row-gap: 52px;
  }
}
@media (min-width: 1150px) {
  .catalog__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 51px;
  }
}

.catalog__item {
  display: grid;
  padding-bottom: 26px;
  border: 1px solid #ececec;
  transition: box-shadow 0.2s ease-out;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .catalog__item {
    padding: 0;
    padding-right: 33px;
    column-gap: 32px;
    grid-template-rows: min-content 1fr min-content;
    grid-template-columns: auto 1fr;
  }
}
@media (min-width: 1150px) {
  .catalog__item {
    padding-right: 0;
    grid-template-columns: 1fr;
    grid-template-rows: 459px min-content 1fr min-content;
  }
}
.catalog__item:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.catalog__item:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

.catalog__link-image,
.catalog__link-title,
.catalog__description {
  grid-column: 1/-1;
}

.catalog__image {
  width: 100%;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .catalog__image {
    width: 334px;
    height: 100%;
    object-fit: cover;
  }
}
@media (min-width: 1150px) {
  .catalog__image {
    width: 316px;
  }
}

.catalog__link-image {
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .catalog__link-image {
    margin-bottom: 0;
    grid-column: 1;
    grid-row: 1/4;
  }
}
@media (min-width: 1150px) {
  .catalog__link-image {
    grid-row: 1;
  }
}

.catalog__link-title {
  margin: 0 27px 4px 27px;
  justify-self: start;
}
@media (min-width: 768px) {
  .catalog__link-title {
    margin: 27px 25px 7px 0;
    grid-column: auto/-1;
  }
}
@media (min-width: 1150px) {
  .catalog__link-title {
    margin: 30px 50px 5px 27px;
    grid-row: 2;
    grid-column: 1;
  }
}
.catalog__link-title:hover .title {
  color: rgba(0, 0, 0, 0.6);
}
.catalog__link-title:active .title {
  color: rgba(0, 0, 0, 0.3);
}

.catalog__title {
  font-size: 17px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .catalog__title {
    font-size: 20px;
  }
}

.catalog__description {
  position: relative;
  margin: 0 27px 17px 27px;
  padding-bottom: 19px;
  font-size: 13px;
  line-height: 30px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .catalog__description {
    margin: 0 0 25px 0;
    padding: 0;
    font-size: 15px;
    grid-column: auto/-1;
  }
}
@media (min-width: 1150px) {
  .catalog__description {
    margin-right: 34px;
    margin-left: 27px;
    padding-bottom: 11px;
    grid-row: 3;
    grid-column: 1;
  }
}

.catalog__price {
  margin-top: 5px;
  padding-left: 26px;
  color: #000000;
  grid-column: 1;
}
@media (min-width: 768px) {
  .catalog__price {
    margin-top: 0;
    margin-bottom: 25px;
    padding: 0;
    padding-right: 50px;
    font-size: 17px;
    grid-row: 3;
    grid-column: auto/-1;
  }
}
@media (min-width: 1150px) {
  .catalog__price {
    padding-left: 26px;
    grid-row: 4;
    grid-column: 1;
  }
}

.catalog__link-basket {
  position: relative;
  margin-top: 5px;
  margin-right: 26px;
  grid-column-end: -1;
  justify-self: end;
}
.catalog__link-basket::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .catalog__link-basket {
    align-self: start;
    margin: 0;
    margin-top: -2px;
    padding: 0;
    grid-column: 2;
    grid-row: 3;
  }
}
@media (min-width: 1150px) {
  .catalog__link-basket {
    margin-top: 0;
    margin-right: 32px;
    grid-row: 4;
    grid-column: 1;
  }
}
.catalog__link-basket:hover .catalog__svg {
  fill: #63d1bb;
}
.catalog__link-basket:active .catalog__svg {
  fill: rgba(99, 209, 187, 0.3);
}

.catalog__svg {
  display: block;
  fill: rgba(0, 0, 0, 0.2);
}

.contacts {
  padding: 38px 0 39px 0;
}
@media (min-width: 768px) {
  .contacts {
    position: relative;
    padding: 69px 0 57px 0;
  }
}
@media (min-width: 1150px) {
  .contacts {
    display: grid;
    width: 1050px;
    margin: 0 auto;
    padding: 71px 0 105px 0;
    grid-template-rows: 1fr auto;
    grid-template-columns: 174px auto 1fr;
  }
}

.contacts__content {
  display: grid;
  max-width: 460px;
  margin: 0 auto 62px;
  padding: 0 30px;
  font-size: 14px;
  line-height: 30px;
  column-gap: 20px;
  grid-template-columns: 57px 1fr;
  row-gap: 29px;
}
@media (min-width: 768px) {
  .contacts__content {
    max-width: 100%;
    margin-bottom: 49px;
    padding: 0 50px;
    font-size: 17px;
    row-gap: 0;
    grid-template-columns: auto 0.9fr auto 1fr;
  }
}
@media (min-width: 1150px) {
  .contacts__content {
    margin-bottom: 84px;
    padding: 0;
    column-gap: 0;
    grid-column: 2;
    grid-template-columns: 78px 242px;
  }
}

.contacts__title {
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .contacts__title {
    margin-bottom: 49px;
  }
}
@media (min-width: 1150px) {
  .contacts__title {
    margin-bottom: 35px;
    padding-top: 79px;
    grid-column: 1/3;
  }
}

.contacts__flag {
  display: none;
}
@media (min-width: 768px) {
  .contacts__flag {
    position: absolute;
    top: 46px;
    right: 53px;
    display: block;
    width: 47px;
    height: 81px;
    background-image: url("../img/svg/flag.svg");
    background-repeat: no-repeat;
  }
}
@media (min-width: 1150px) {
  .contacts__flag {
    position: static;
    margin-top: 62px;
    grid-column: 1;
    justify-self: center;
  }
}

.contacts__email-text,
.contacts__address-text {
  grid-column: 1;
}

.contacts__email-text {
  word-break: break-all;
}
@media (min-width: 768px) {
  .contacts__email-text {
    grid-column: 1;
  }
}
@media (min-width: 1150px) {
  .contacts__email-text {
    margin-bottom: 29px;
    padding-right: 10px;
  }
}

.contacts__address-text {
  word-break: break-all;
}
@media (min-width: 768px) {
  .contacts__address-text {
    grid-column: 3;
  }
}
@media (min-width: 1150px) {
  .contacts__address-text {
    padding-right: 10px;
    grid-column: 1;
  }
}

.contacts__link-mailto,
.contacts__address {
  grid-column-end: -1;
}

.contacts__link-mailto {
  position: relative;
  align-self: start;
  color: #444444;
  justify-self: start;
}
@media (min-width: 768px) {
  .contacts__link-mailto {
    align-self: start;
    padding-left: 4px;
    grid-column: 2;
  }
}
@media (min-width: 1150px) {
  .contacts__link-mailto {
    margin-bottom: 29px;
    padding-left: 0;
  }
}
.contacts__link-mailto::after {
  content: "";
  position: absolute;
  bottom: 4px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #62d1ba;
}
@media (min-width: 768px) {
  .contacts__link-mailto::after {
    bottom: 0;
  }
}
@media (min-width: 1150px) {
  .contacts__link-mailto::after {
    bottom: -4px;
  }
}
.contacts__link-mailto:hover {
  color: #62d1ba;
}
.contacts__link-mailto:active::after {
  background-color: rgba(98, 209, 186, 0.1);
}

.contacts__address {
  font-style: normal;
}
@media (min-width: 768px) {
  .contacts__address {
    padding-left: 7px;
    grid-column: 4;
  }
}
@media (min-width: 1150px) {
  .contacts__address {
    padding-left: 0;
    grid-column: 2;
  }
}

.contacts__map {
  height: 457px;
  margin-bottom: 39px;
}
.no-js .contacts__map {
  height: auto;
}
@media (min-width: 768px) {
  .contacts__map {
    margin-bottom: 54px;
  }
}
@media (min-width: 1150px) {
  .contacts__map {
    height: 100%;
    margin-bottom: 0;
    margin-left: 29px;
    grid-column: 3;
    grid-row: 1/3;
  }
}

.contacts__features {
  display: none;
}
.no-js .contacts__features {
  display: block;
}

.contacts__image {
  display: block;
  width: 100%;
  height: 457px;
  object-fit: cover;
}

.contacts__yandex-map {
  width: 100%;
  height: 457px;
  border: 0;
}
.no-js .contacts__yandex-map {
  display: none;
}
@media (min-width: 1150px) {
  .contacts__yandex-map {
    width: 527px;
    height: 100%;
  }
}

.contacts__link {
  width: calc(100% - 60px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contacts__link {
    width: 230px;
  }
}
@media (min-width: 1150px) {
  .contacts__link {
    margin: 0;
    grid-column: 2;
  }
}

.hero {
  position: relative;
  background-color: #f9f9f9;
}
@media (min-width: 768px) {
  .hero::after {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 50%;
    z-index: 1;
    width: 100%;
    height: 24px;
    background-image: url("../img/svg/zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: bottom center;
    background-size: 770px 24px;
    transform: translateX(-50%);
    pointer-events: none;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    bottom: -17px;
    height: 34px;
    background-size: 1250px 34px;
  }
}

.hero__wrapper {
  background-image: url("../img/backgrounds/hero-mobile.jpg");
  background-repeat: no-repeat;
  background-position: bottom 133px center;
}
@media (min-resolution: 2dppx) {
  .hero__wrapper {
    background-image: url("../img/backgrounds/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .hero__wrapper {
    position: relative;
    padding: 107px 50px 0 50px;
    background-image: url("../img/backgrounds/hero-desktop.jpg");
    background-position: bottom 3px center;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero__wrapper {
    background-image: url("../img/backgrounds/hero-desktop@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .hero__wrapper {
    width: 1150px;
    margin: 0 auto;
    padding-top: 108px;
    background-position: bottom center;
  }
}

.hero__title {
  padding: 73px 30px 120px 30px;
  font-size: 27px;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__title {
    padding: 0;
    padding-bottom: 371px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    width: 700px;
    margin: 0 auto;
    padding-bottom: 355px;
    font-size: 53px;
    line-height: 57px;
  }
}

.hero__list {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .hero__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.hero__item {
  flex-grow: 1;
}
.hero__item:nth-child(even) .hero__link,
.hero__item:nth-child(even) .hero__text {
  background-color: #56c3b2;
}
.hero__item:hover:nth-child(even) .hero__link,
.hero__item:hover:nth-child(even) .hero__text {
  background-color: #46c1ae;
}
@media (min-width: 768px) {
  .hero__item {
    width: 50%;
  }
  .hero__item:nth-child(even) .hero__link,
.hero__item:nth-child(even) .hero__text {
    background-color: #63d1bb;
  }
  .hero__item:hover:nth-child(even) .hero__link,
.hero__item:hover:nth-child(even) .hero__text {
    background-color: #59d5bc;
  }
  .hero__item:nth-child(4n+2) .hero__link,
.hero__item:nth-child(4n+2) .hero__text, .hero__item:nth-child(4n-1) .hero__link,
.hero__item:nth-child(4n-1) .hero__text {
    background-color: #56c3b2;
  }
  .hero__item:hover:nth-child(4n+2) .hero__link,
.hero__item:hover:nth-child(4n+2) .hero__text, .hero__item:hover:nth-child(4n-1) .hero__link,
.hero__item:hover:nth-child(4n-1) .hero__text {
    background-color: #46c1ae;
  }
}

.hero__link {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  font-weight: 700;
  font-size: 19px;
  color: #ffffff;
  background-color: #63d1bb;
  transition: 0.2s;
}
.hero__link:focus {
  z-index: 1;
}
.hero__link:hover .hero__text {
  background-color: #59d5bc;
  transform: translateY(-5px);
}
.hero__link:hover .hero__text::after {
  opacity: 0.7;
}
.hero__link:active .hero__text::after {
  opacity: 0.3;
