  background-repeat: no-repeat;
  background-position: center;
}
.reviews__control:hover::before {
  opacity: 0.6;
}
.reviews__control:active::before {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .reviews__control {
    width: 75px;
    border: none;
  }
  .reviews__control:not(:last-child) {
    margin-right: 12px;
  }
}
.reviews__control--right {
  border-left: none;
}
.reviews__control--right::before {
  transform: scale(-1, 1);
}
.reviews__control:disabled {
  opacity: 0.3;
  pointer-events: none;
}

.reviews__link {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .reviews__link {
    align-self: start;
    max-width: 220px;
    margin-top: 8px;
    padding-right: 38px;
    padding-left: 38px;
    grid-column-end: -1;
    grid-row: 1;
  }
}
@media (min-width: 1150px) {
  .reviews__link {
    margin-top: 1px;
    margin-bottom: 8px;
  }
}

.zigzag {
  background-image: url("../img/svg/zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: center bottom;
  background-size: auto 7px;
}
@media (min-width: 768px) {
  .zigzag {
    background-size: auto 17px;
  }
}
@media (min-width: 1150px) {
  .zigzag {
    background-size: auto 23px;
  }
}